/* Base Navbar Styles */
.navbar {
  padding: 1rem 2rem;
  background-color: #343a40; /* Dark background */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Adds shadow for depth */
  transition: background-color 0.3s ease;
}

.navbar:hover {
  background-color: #23272b; /* Darker shade on hover */
}

/* Navbar Brand - Logo */
.navbar-brand.logo {
  font-size: 1.8rem;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: color 0.3s ease;
}

.navbar-brand.logo:hover {
  color: #ffdd57; /* Yellow color on hover */
}

/* Navbar Links */
.navbar-nav {
  display: flex; /* Aligns items horizontally */
  justify-content: center; /* Centers the items horizontally */
  gap: 1.5rem; /* Adds space between the items */
}

.nav-link {
  font-size: 1.1rem;
  color: white !important;
  padding: 0.75rem 1rem;
  transition: color 0.3s ease, background-color 0.3s ease;
  text-transform: capitalize; /* Adds a more professional look */
}

.nav-link:hover {
  color: #ffdd57 !important; /* Yellow color on hover */
  background-color: rgba(255, 221, 87, 0.1); /* Soft yellow background on hover */
  border-radius: 5px; /* Rounded corners on hover */
}

/* Active Link */
.nav-link.active {
  color: #ffdd57 !important; /* Active link color */
  font-weight: bold; /* Makes active link bold */
}

/* Navbar Toggle Button (Hamburger Menu) */
.navbar-toggler {
  border: none;
  background-color: transparent;
}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255, 255, 255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

/* Mobile Menu Styles */
@media (max-width: 992px) {
  .navbar-collapse {
    background-color: #343a40; /* Dark background for mobile menu */
    padding: 1rem;
    margin-top: 0.5rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Adds a shadow for mobile menu */
  }

  .navbar-nav {
    flex-direction: column; /* Stack items vertically on mobile */
    align-items: center; /* Center align items */
    gap: 1rem; /* Adds space between links */
  }

  .nav-item {
    width: 100%; /* Ensure the links take full width */
    text-align: center;
  }

  .nav-link {
    padding: 0.75rem;
    width: 100%; /* Make the links full width */
    text-align: center; /* Center the text */
  }
}
