/* Main container for the notes */
.notes-container {
   max-width: 900px;
   margin: 20px auto;
   padding: 20px;
   background-color: #f9f9f9;
   border-radius: 8px;
 }
 
 /* Title of the notes section */
 .notes-title {
   font-size: 24px;
   font-weight: bold;
   color: #333;
   margin-bottom: 15px;
 }
 
 /* Search bar styling */
 .search-bar {
   width: 100%;
   padding: 10px;
   margin-bottom: 20px;
   font-size: 16px;
   border: 1px solid #ddd;
   border-radius: 4px;
 }
 
 /* No notes available message */
 .no-notes {
   font-size: 18px;
   color: #777;
 }
 
 /* List of notes */
 .notes-list {
   list-style: none;
   padding: 0;
   margin: 0;
 }
 
 /* Individual note card */
 .note-card {
   background-color: #fff;
   padding: 15px;
   margin-bottom: 15px;
   border-radius: 8px;
   box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
 }
 
 /* Note title */
 .note-title {
   font-size: 20px;
   font-weight: bold;
   color: #333;
 }
 
 /* Note description */
 .note-description {
   font-size: 16px;
   color: #555;
   margin: 10px 0;
 }
 
 /* Note link styling */
 .note-link {
   display: inline-block;
   padding: 5px 10px;
   margin-top: 10px;
   background-color: #007bff;
   color: #fff;
   border-radius: 4px;
   text-decoration: none;
 }
 
 .note-link:hover {
   background-color: #0056b3;
 }
 
 /* Edit and Delete buttons */
 button {
   padding: 8px 16px;
   font-size: 14px;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   transition: background-color 0.3s ease;
   margin: 5px;
 }
 
 /* Green Edit Button */
 button.edit-button {
   background-color: #28a745; /* Green color */
 }
 
 button.edit-button:hover {
   background-color: #218838; /* Darker green on hover */
 }
 
 /* Red Delete Button */
 button.delete-button {
   background-color: #dc3545; /* Red color */
 }
 
 button.delete-button:hover {
   background-color: #c82333; /* Darker red on hover */
 }
 
 /* Input and textarea styling when editing */
 input[type="text"],
 textarea {
   width: 100%;
   padding: 10px;
   font-size: 16px;
   margin: 10px 0;
   border-radius: 4px;
   border: 1px solid #ccc;
 }
 
 /* Increased size for textarea when editing */
 textarea {
   min-height: 150px;
 }
 
 /* Button styling when editing */
 button {
   padding: 10px 20px;
   margin: 5px;
   font-size: 16px;
 }
 