/* Container */
.notes-container {
   max-width: 800px;
   margin: 20px auto;
   padding: 20px;
   background: #ffffff;
   border-radius: 10px;
   box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
   text-align: center;
 }
 
 /* Title */
 .notes-title {
   font-size: 26px;
   font-weight: bold;
   margin-bottom: 15px;
   color: #222;
 }
 
 /* Search Bar */
 .search-bar {
   width: 100%;
   padding: 12px;
   margin-bottom: 20px;
   border: 2px solid #ccc;
   border-radius: 8px;
   font-size: 16px;
   transition: 0.3s;
 }
 
 .search-bar:focus {
   border-color: #007bff;
   outline: none;
   box-shadow: 0px 0px 8px rgba(0, 123, 255, 0.5);
 }
 
 /* No Notes */
 .no-notes {
   font-size: 16px;
   color: #777;
 }
 
 /* Notes List */
 .notes-list {
   list-style: none;
   padding: 0;
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
   gap: 15px;
 }
 
 /* Note Card */
 .note-card {
   background: #f9f9f9;
   padding: 15px;
   border-radius: 8px;
   text-align: left;
   box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
   transition: 0.3s ease-in-out;
   border-left: 5px solid #007bff;
 }
 
 .note-card:hover {
   transform: translateY(-5px);
   box-shadow: 0px 5px 12px rgba(0, 0, 0, 0.15);
 }
 
 /* Note Title */
 .note-title {
   font-size: 18px;
   font-weight: bold;
   margin-bottom: 5px;
   color: #333;
 }
 
 /* Note Description */
 .note-description {
   font-size: 14px;
   color: #555;
   margin-bottom: 8px;
 }
 
 /* View File Link */
 .note-link {
   display: inline-block;
   text-decoration: none;
   color: #007bff;
   font-weight: bold;
   transition: 0.3s ease-in-out;
 }
 
 .note-link:hover {
   text-decoration: underline;
   color: #0056b3;
 }
 