/* UploadForm Styles */
.upload-form-container {
   max-width: 500px;
   margin: 20px auto;
   padding: 20px;
   background: #f9f9f9;
   border-radius: 8px;
   box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
   text-align: center;
 }
 
 .upload-form-title {
   font-size: 1.8rem;
   font-weight: bold;
   margin-bottom: 15px;
   color: #333;
   border-bottom: 2px solid #ddd;
   padding-bottom: 10px;
 }
 
 .upload-form {
   display: flex;
   flex-direction: column;
   gap: 15px;
 }
 
 .form-group {
   text-align: left;
 }
 
 .form-group label {
   font-weight: bold;
   display: block;
   margin-bottom: 5px;
   color: #444;
 }
 
 .form-group input,
 .form-group textarea {
   width: 100%;
   padding: 10px;
   border: 1px solid #ccc;
   border-radius: 5px;
   font-size: 1rem;
 }
 
 .form-group textarea {
   min-height: 100px;
   resize: vertical;
 }
 
 .upload-button {
   background: #007bff;
   color: white;
   padding: 12px;
   font-size: 1rem;
   border: none;
   border-radius: 5px;
   cursor: pointer;
   transition: 0.3s ease;
 }
 
 .upload-button:hover {
   background: #0056b3;
 }
 
 /* Responsive Design */
 @media (max-width: 600px) {
   .upload-form-container {
     width: 90%;
   }
 }
 