/* Center the register box */
.register-container {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100vh;
   background: #f4f4f4;
 }
 
 /* Register box styling */
 .register-box {
   background: white;
   padding: 25px;
   border-radius: 8px;
   box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
   width: 350px;
   text-align: center;
 }
 
 /* Title */
 .register-title {
   font-size: 24px;
   margin-bottom: 15px;
   color: #333;
 }
 
 /* Error Message */
 .error-message {
   color: red;
   font-size: 14px;
   margin-bottom: 10px;
 }
 
 /* Input Group */
 .input-group {
   text-align: left;
   margin-bottom: 15px;
 }
 
 .input-group label {
   display: block;
   font-size: 14px;
   margin-bottom: 5px;
   color: #555;
 }
 
 .input-group input {
   width: 100%;
   padding: 10px;
   border: 1px solid #ccc;
   border-radius: 5px;
   transition: 0.3s;
 }
 
 .input-group input:focus {
   border-color: #007bff;
   outline: none;
   box-shadow: 0px 0px 5px rgba(0, 123, 255, 0.5);
 }
 
 /* Register Button */
 .register-button {
   width: 100%;
   background: #007bff;
   color: white;
   padding: 10px;
   border: none;
   border-radius: 5px;
   font-size: 16px;
   cursor: pointer;
   transition: 0.3s;
 }
 
 .register-button:hover {
   background: #0056b3;
 }
 
 /* Login Link */
 .login-link {
   margin-top: 10px;
   font-size: 14px;
 }
 
 .login-link a {
   color: #007bff;
   text-decoration: none;
 }
 
 .login-link a:hover {
   text-decoration: underline;
 }
 